<template>
  <div id="mapContainer"></div>
</template>

<script>
export default {
  data() {
    return {
      mapKey: "38aa4487c263564de7dd2f467e218645",
      mapId: "gaodemap",
      map: null,
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.loadMap()
        .then(() => {
          this.map = new AMap.Map("mapContainer", {
            resizeEnable: true,
            zoom: 13,
          });
          this.$emit("mounted", this.map);
        })
        .catch(() => {});
    },
    loadMap() {
      if (document.getElementById(this.mapId)) {
        return Promise.resolve();
      }
      return new Promise((resovle, reject) => {
        const script = document.createElement("script");
        script.id = this.mapId;
        script.src = `https://webapi.amap.com/maps?v=1.4.15&key=${this.mapKey}`;
        script.onload = function () {
          resovle();
        };
        script.onerror = function () {
          reject();
        };
        document.head.appendChild(script);
      });
    },
    location() {
      return new Promise((resolve, reject) => {
        AMap.plugin("AMap.Geolocation", function () {
          var geolocation = new AMap.Geolocation({
            // 是否使用高精度定位，默认：true
            enableHighAccuracy: true,
            // 设置定位超时时间，默认：无穷大
            timeout: 10000,
            // 定位按钮的停靠位置的偏移量，默认：Pixel(10, 20)
            buttonOffset: new AMap.Pixel(10, 20),
            //  定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
            zoomToAccuracy: true,
            //  定位按钮的排放位置,  RB表示右下
            buttonPosition: "RB",
            // showButton: true,
            // showMarker: true
          });

          geolocation.getCurrentPosition();
          AMap.event.addListener(geolocation, "complete", onComplete);
          AMap.event.addListener(geolocation, "error", onError);

          function onComplete(data) {
            // data是具体的定位信息
            resolve(data);
          }

          function onError(data) {
            // 定位出错
            reject(data);
          }
        });
      });
    },
    circle(options) {
      return new AMap.Circle({
        center: new AMap.LngLat(options.lng + "", options.lat + ""), // 圆心位置
        radius: 1050, //半径
        strokeColor: "#F33", //线颜色
        strokeOpacity: 1, //线透明度
        strokeWeight: 3, //线粗细度
        fillColor: "#ee2200", //填充颜色
        fillOpacity: 0.35, //填充透明度
      });
    },
    marker(options) {
      // 构造点标记
      return new AMap.Marker({
        icon: "data:image/png;base64,",
        position: [options.lng + "", options.lat + ""],
      });
    },
  },
};
</script>

<style scoped>
#mapContainer {
  width: 100%;
  height: 100%;
}
</style>