<template>
  <div class="discuss-container">
    <h2 class="text-center">全部评论</h2>
    <div class="discuss-list">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <van-cell v-for="item in list" :key="item">
          <discussItem @clickChat="replayList">
            <discussItem @clickChat="replayList" />
            <discussItem @clickChat="replayList" />
            <discussItem @clickChat="replayList" />
            <discussItem @clickChat="replayList" />
          </discussItem>
        </van-cell>
      </van-list>
    </div>
    <div class="discuss-footer">
      <van-field
        v-model="value"
        rows="1"
        autosize
        type="textarea"
        placeholder="请输入评论"
      >
        <template #button>
          <van-button size="small" type="primary">发布</van-button>
        </template>
      </van-field>
    </div>
    <!-- <van-popup v-model="show" position="bottom" closeable :style="{ height: '80vh' }">
      <replayList />
    </van-popup> -->
    <van-popup
      v-model="show"
      position="bottom"
      closeable
      :style="{ height: '30vh' }"
    >
      <div
        class="flex justify-center align-center"
        style="height: 30vh; overflow-y: auto"
      >
        <van-field
          rows="3"
          autosize
          type="textarea"
          placeholder="请输入回复内容"
        >
          <template #button>
            <van-button size="small" type="primary">回复</van-button>
          </template>
        </van-field>
      </div>
    </van-popup>
  </div>
</template>

<script>
import discussItem from "@/components/discussItem";
import replayList from "@/components/replayList";

export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      value: "",
      show: false,
    };
  },
  components: {
    discussItem,
    replayList,
  },
  methods: {
    replayList() {
      this.show = true;
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
  },
};
</script>

<style lang="less" scoped>
@import "@/assets/style/custom.less";
@import "@/assets/style/theme.less";
.discuss-container {
  position: relative;
}
h2 {
  height: 5vh;
  line-height: 5vh;
}
.discuss-list {
  height: 70vh;
  overflow-y: auto;
  padding-bottom: 4.5rem;
}
.discuss-footer {
  height: 9rem;
  background: #fff;
  border-top: 1px solid #ccc;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
}
</style>