<script>
import gaodemap from "@/components/map";
import { Toast } from "vant";
export default {
  components: {
    gaodemap,
  },
  computed: {
    LngLat() {
      if (!AMap) {
        return;
      }
      return new AMap.LngLat(this.location.lng, this.location.lat);
    },
  },
  data() {
    return {
      map: null,
      locationMarker: null,
      circle: null,
      location: {
        lat: 30.672013,
        lng: 104.044277,
      },
    };
  },
  methods: {
    async mapMounted(map) {
      this.map = map;
      this.init();
      this.locationMarker = await this.$refs.map.marker({
        ...this.location,
      });
      this.map.add(this.locationMarker);
    },
    async init() {
      const options = {
        lat: 30.56975,
        lng: 104.06901,
      };
      this.circle = await this.$refs.map.circle(options);
      console.log(this.circle);
      this.map.add(this.circle);
    },
    geolocation() {
      return new Promise((resolve, reject) => {
        this.$refs.map
          .location()
          .then((r) => {
            resolve(r);
            // alert(r.formattedAddress);
          })
          .catch((e) => {
            console.log(e);
            reject(e);
            Toast.fail(
              JSON.stringify(e) || "定位失败，请确定是否允许浏览器定位"
            );
          });
      });
    },
    removeMarker() {
      this.map.remove(this.locationMarker);
    },
    async moveLocation() {
      const { position } = await this.geolocation();
      const options = {
        lat: position.lat,
        lng: position.lng,
      };
      this.location = { ...options };
      if (this.locationMarker) {
        this.removeMarker();
        this.locationMarker = null;
      }
      this.locationMarker = await this.$refs.map.marker(options);
      this.map.add(this.locationMarker);
      this.map.setCenter([position.lng, position.lat]);
    },
    containsLocation() {
      if (this.circle && this.locationMarker) {
        // const LngLat = new AMap.LngLat(this.location.lat, this.location.lng);
        if (this.circle.contains(this.LngLat)) {
          Toast.success("签到成功");
        } else {
          Toast.fail("您不在签到范围内，请到达签到位置");
        }
      } else {
        Toast.fail("定位信息不准确，请重试");
      }
    },
  },
};
</script>

