import Login from '@/components/Login'
import Vue from 'vue'

const LoginConstructor = Vue.extend(Login)

const options = {
    data() {
        return {}
    }
}


// options 内部同组件内部生命周期一样，但组件内部优先执行
const instance = new LoginConstructor(options)

instance.$mount()

document.body.appendChild(instance.$el)

export default function install() {
    Vue.prototype.$Login = instance
}