<template>
  <div>
    <quillEditor
      class="ql-container"
      v-model="content"
      :options="editorOption"
      @focus="onEditorFocus($event)"
      ref="editor"
    ></quillEditor>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

export default {
  props: {
    editorOption: {
      type: Object,
      default() {
        return {
          readOnly: true,
          placeholder: "",
          toolbar: false,
        };
      },
    },
    content: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      showMoreTool: false,
      showBold: false,
      showItalic: false,
      showIns: false,
      showHeader: false,
      showCenter: false,
      showRight: false,
      showSettingLayer: false,
      activeColor: "#F56C6C",
      file: null,
    };
  },
  components: {
    quillEditor,
  },
  mounted() {
    this.editorCtx.enable(false);
  },
  computed: {
    editorCtx() {
      return this.$refs.editor.quill;
    },
  },
  methods: {
    onEditorFocus(event) {
      event.enable(false);
    },
  },
};
</script>

