import Vue from 'vue';
import VueRouter from 'vue-router';
import notFound from '@/pages/404';
import Home from '@/pages/Home';
import Answer from '@/pages/Answer';
import Exam from '@/pages/Exam';
import Index from '@/pages/Index';
import User from '@/pages/User';
import Main from '@/pages/Main';
import AnswerDetail from '@/pages/AnswerDetail'
import Video from '@/pages/Video'
import SignIn from '@/pages/SignIn'

Vue.use(VueRouter);

const routes = [
  {
    path: '/', component: Index, redirect: '/home',
    children: [
      { path: '/home', component: Home },
      { path: '/user', component: User },
    ]
  },
  {
    path: '/main', component: Main, redirect: '/home',
    children: [
      { path: '/main/answer', component: Answer, meta: { title: "答题" } },
      { path: '/main/exam', component: Exam, meta: { title: "考试" } },
      { path: '/main/answerdetail', component: AnswerDetail, meta: { title: "回答详情" } },
      { path: '/main/signIn', component: SignIn, meta: { title: "签到" } },
    ]
  },
  {
    path: '/video', component: Video
  },
  { path: '/404', component: notFound },
  { path: '*', redirect: '/404' }

];

export default new VueRouter({
  routes,
});
