<template>
  <div>
    <van-search
      v-model="value"
      show-action
      placeholder="请输入搜索关键词"
      @search="onSearch"
    >
      <template #action>
        <van-icon
          name="add"
          size="30"
          :color="primaryColor"
          @click="showAction"
        />
      </template>
    </van-search>
    <van-tabs sticky v-model="active" animated swipeable :color="primaryColor">
      <van-tab
        v-for="index in [
          '科学',
          '民主',
          '富强',
          '和谐',
          '道德',
          '法制',
          '人民',
        ]"
        :title="index"
        :key="index"
      >
        <template #title>
          <span class="font-16">{{ index }}</span>
        </template>
        <div style="padding-bottom: 5rem">
          <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
            <van-list
              v-model="loading"
              :finished="finished"
              finished-text="没有更多了"
              @load="onLoad"
            >
              <van-skeleton
                title
                :row="3"
                :loading="initLoading"
                v-for="index in 5"
                :key="index"
                class="mt-2"
              />
              <div
                class="cell-item"
                v-for="(item, index) in list"
                :key="index"
                @click="enterDetail"
              >
                <div class="cell-title">{{ item.title }}</div>
                <div class="cell-body">
                  <van-row
                    type="flex"
                    justify="space-between"
                    :gutter="item.url ? 20 : 0"
                    align="center"
                  >
                    <van-col
                      :span="item.url ? 16 : 24"
                      class="van-multi-ellipsis--l3"
                      >{{ item.description }}</van-col
                    >
                    <van-col class="flex justify-end " v-if="item.url" :span="item.url ? 8 : 0">
                      <van-image
                        width="100"
                        height="100"
                        lazy-load
                        :src="item.url"
                        v-if="item.url"
                      />
                    </van-col>
                  </van-row>
                </div>
                <div class="font-14 cell-footer">
                  <span class="mr-1"
                    ><span style="padding-right: 2px">220</span
                    ><van-icon name="like" color="#e54d42"
                  /></span>
                  <span
                    ><span style="padding-right: 2px">111</span
                    ><van-icon name="chat"
                  /></span>
                </div>
              </div>
            </van-list>
          </van-pull-refresh>
        </div>
      </van-tab>
    </van-tabs>

    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" />

    <van-popup v-model="showPop" position="bottom" :style="{ height: '80vh' }">
      <submitQuestion />
    </van-popup>
  </div>
</template>

<script>
import submitQuestion from "@/components/submitQuestion";
import { getImgByHtml } from "@/utils/util";

export default {
  data() {
    return {
      value: "",
      active: 0,
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      show: false,
      actions: [{ name: "提个问题" }],
      showPop: false,
      initLoading: true,
      content: `<p>erereerereerereerereerereerereerereerereerereerereerere</p><p><br></p><p><br></p><p><img src="data:image/png;base64,"></p><p><br></p><p><br></p><p>efdfyyyyyy</p>`,
    };
  },
  components: {
    submitQuestion,
  },
  beforeDestroy() {
    this.list = [];
    this.refreshing = false;
  },
  methods: {
    showAction() {
      this.show = true;
    },

    onLoad() {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = [];
          this.refreshing = false;
        }

        for (let i = 0; i < 10; i++) {
          this.list.push({
            title: "为什么都说React比Vue难？",
            description:
              "React难在工程意义上的难，REact的核心特点是：核心团队只维护和辛苦，生态周边包含一些库",
            url:
              i % 2 === 0 && this.content ? getImgByHtml(this.content) : null,
          });
        }
        this.loading = false;

        if (this.list.length >= 40) {
          this.finished = true;
        }
        this.initLoading = false;
      }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    onSearch() {},
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.show = false;
      this.showPop = true;
      // this.$router.push('/main/answer')
    },
    enterDetail() {
      this.$router.push("/main/answerdetail");
    },
  },
  created() {
    this.onLoad();
  },
};
</script>

