<template>
  <div>
    <quillEditor
      class="ql-container"
      v-model="content"
      :options="editorOption"
      @blur="onEditorBlur($event)"
      @focus="onEditorFocus($event)"
      @ready="onEditorReady($event)"
      ref="editor"
    ></quillEditor>
    <!-- 操作工具 -->
    <div class="tool-div">
      <div class="tool">
        <jinIcon
          class="single"
          type="&#xe6f3;"
          font-size="2rem"
          title="插入图片"
          @click="insertImage"
        ></jinIcon>
        <van-icon
          name="play-circle-o"
          color="#666666"
          @click="selectVideo"
          size="2rem"
          style="font-weight: bold"
        />
        <jinIcon
          class="single"
          type="&#xe6f9;"
          font-size="2rem"
          title="修改文字样式"
          @click="showMore"
          :color="showMoreTool ? activeColor : '#666666'"
        ></jinIcon>
        <!-- <jinIcon class="single" type="&#xe6eb;" font-size="44px" title="分割线" @click="insertDivider"></jinIcon> -->
        <jinIcon
          class="single"
          type="&#xe6e8;"
          font-size="2rem"
          title="撤销"
          @click="undo"
        ></jinIcon>
        <jinIcon
          class="single"
          type="&#xe705;"
          font-size="2rem"
          title="重做"
          @click="redo"
        ></jinIcon>
        <jinIcon
          class="single"
          type="&#xeb8a;"
          font-size="2rem"
          title="设置"
          @click="showSetting"
        ></jinIcon>
      </div>
      <!-- 文字相关操作 -->
      <div class="font-more" :style="{ height: showMoreTool ? '5rem' : 0 }">
        <jinIcon
          class="single"
          type="&#xe6e7;"
          font-size="2rem"
          title="加粗"
          @click="setBold"
          :color="showBold ? activeColor : '#666666'"
        ></jinIcon>
        <jinIcon
          class="single"
          type="&#xe6fe;"
          font-size="2rem"
          title="斜体"
          @click="setItalic"
          :color="showItalic ? activeColor : '#666666'"
        ></jinIcon>
        <!-- <jinIcon class="single" type="&#xe6f8;" font-size="44px" title="分割线" @click="setIns" :color="showIns ? activeColor : '#666666'"></jinIcon> -->
        <jinIcon
          class="single"
          type="&#xe6e3;"
          font-size="2rem"
          title="标题"
          @click="setHeader"
          :color="showHeader ? activeColor : '#666666'"
        ></jinIcon>
        <jinIcon
          class="single"
          type="&#xe6f1;"
          font-size="2rem"
          title="居中"
          @click="setCenter"
          :color="showCenter ? activeColor : '#666666'"
        ></jinIcon>
        <jinIcon
          class="single"
          type="&#xe6ed;"
          font-size="2rem"
          title="居右"
          @click="setRight"
          :color="showRight ? activeColor : '#666666'"
        ></jinIcon>
      </div>
      <div
        class="setting-layer-mask"
        v-if="showSettingLayer"
        @click="showSetting"
      ></div>
      <!-- <div class="setting-layer" v-if="showSettingLayer">
        <div class="single" @click="release(true)">
          <jinIcon class="icon" type="&#xe639;"></jinIcon>
          <div>公开发布</div>
        </div>
        <div class="single" @click="release(false)">
          <jinIcon class="icon" type="&#xe655;"></jinIcon>
          <div>私密保存</div>
        </div>
      </div> -->
    </div>
    <!-- 选择文件 -->
    <input
      type="file"
      id="file"
      @change="changeFile"
      accept="image/*"
      style="display: none"
    />
    <input
      type="file"
      id="fileVideo"
      @change="changeFileVideo"
      accept="video/*"
      style="display: none"
    />
  </div>
</template>

<script>
import jinIcon from "@/components/jin-icons";
import { quillEditor } from "vue-quill-editor";
import "@/utils/quill-video";
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

export default {
  props: {
    editorOption: {
      type: Object,
      default() {
        return {
          readOnly: true,
          placeholder: "填写回答内容",
          toolbar: false,
        };
      },
    },
  },
  computed: {},
  data() {
    return {
      showMoreTool: false,
      showBold: false,
      showItalic: false,
      showIns: false,
      showHeader: false,
      showCenter: false,
      showRight: false,
      showSettingLayer: false,
      activeColor: "#F56C6C",
      content: ``,
      file: null,
      fileVideo: null,
    };
  },
  components: {
    jinIcon,
    quillEditor,
  },
  mounted() {
    this.file = document.querySelector("#file");
    this.fileVideo = document.querySelector("#fileVideo");
  },
  computed: {
    editorCtx() {
      return this.$refs.editor.quill;
    },
  },
  methods: {
    onEditorReady(e) {
      // console.dir(this.editorCtx = document.querySelector('.ql-container').__quill)
    },
    undo() {
      this.editorCtx.history.undo();
      this.editorCtx.blur();
    },
    onEditorBlur(quill) {
      console.log("editor blur!", quill);
    },
    onEditorFocus(quill) {
      console.log("editor focus!", quill);
    },
    onEditorReady(quill) {
      console.log("editor ready!", quill);
    },
    onEditorChange({ quill, html, text }) {
      console.log("editor change!", quill, html, text);
      this.content = html;
    },
    // 插入图片
    insertImage() {
      this.file.click();
    },
    insertDivider() {
      this.editorCtx.insertDivider();
      this.editorCtx.blur();
    },
    redo() {
      this.editorCtx.history.redo();
      this.editorCtx.blur();
    },
    showMore() {
      this.showMoreTool = !this.showMoreTool;
      // this.editorCtx.setContents()
    },
    setBold() {
      this.showBold = !this.showBold;
      this.editorCtx.format("bold", this.showBold ? "bold" : false);
      this.editorCtx.blur();
    },
    setItalic() {
      this.showItalic = !this.showItalic;
      this.editorCtx.format("italic", this.showItalic ? "italic" : false);
      this.editorCtx.blur();
    },
    checkStatus(name, detail, obj) {
      if (detail.hasOwnProperty(name)) {
        this[obj] = true;
      } else {
        this[obj] = false;
      }
    },
    setIns() {
      this.showIns = !this.showIns;
      this.editorCtx.format("ins");
    },
    setHeader() {
      this.showHeader = !this.showHeader;
      this.editorCtx.format("header", this.showHeader ? "H2" : false);
      this.editorCtx.blur();
    },
    setCenter() {
      this.showCenter = !this.showCenter;
      this.editorCtx.format("align", this.showCenter ? "center" : false);
      this.editorCtx.blur();
    },
    setRight() {
      this.showRight = !this.showRight;
      this.editorCtx.format("align", this.showRight ? "right" : false);
      this.editorCtx.blur();
    },
    showSetting() {
      //   this.showSettingLayer = !this.showSettingLayer;
      // const getLength = this.editorCtx.getLength();
      // const data = this.editorCtx.getText();
      console.log(data);
      const data = document.querySelector(".ql-editor").innerHTML;
      this.$emit("submit", data);
    },
    async editFocus() {},
    editBlur() {},
    async changeFile({ target }) {
      const { files } = target;
      const file = files[0];
      target.value = "";
      if (file.size > 2 * 1024 * 1024) {
        alert("上传图片不能超过2MB");
        return;
      }
      const dataBase = await this.fileToBase(file);
      console.log(dataBase);
      const selection = this.editorCtx.getSelection(true);
      this.editorCtx.editor.insertEmbed(selection.index, "image", dataBase);
    },
    fileToBase(file) {
      const filereader = new FileReader();
      return new Promise((r) => {
        filereader.onload = function (data) {
          r(this.result);
        };
        filereader.readAsDataURL(file);
      });
    },
    release(isPublic) {
      this.showSettingLayer = false;
      this.editorCtx.getContents({
        success: (res) => {
          Object.assign(res, {
            isPublic: isPublic,
          });
          this.$emit("editOk", res);
        },
      });
    },
    selectVideo() {
      this.fileVideo.click();
    },
    changeFileVideo({ target }) {
      const { files } = target;
      const file = files[0];
      target.value = "";
      if (file.size > 10 * 1024 * 1024) {
        alert("上传视频不能超过10MB");
        return;
      }
      const selection = this.editorCtx.getSelection(true);
      this.editorCtx.editor.insertEmbed(selection.index, "simpleVideo", {
        url: "https://media.w3.org/2010/05/sintel/trailer.mp4",
        controls: "controls",
        width: "100%",
        height: "100%",
        poster: `https://img01.yzcdn.cn/vant/logo.png`,
      });
    },
  },
};
</script>

