<template>
  <div>
    <router-view></router-view>
    <van-tabbar v-model="active" @change="change" inactive-color="#000">
      <van-tabbar-item
        :icon="tab.icon"
        :name="tab.name"
        v-for="tab in tabList"
        :key="tab.name"
        >{{ tab.label }}</van-tabbar-item
      >
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: "/home",
      tabList: [
        {
          label: "首页",
          name: "/home",
          icon: "home-o",
        },
        {
          label: "签到",
          name: "/main/signIn",
          icon: "home-o",
        },
        {
          label: "团队",
          name: "/main/answer",
          icon: "friends-o",
        },
        {
          label: "我的",
          name: "/user",
          icon: "user-o",
        },
      ],
    };
  },
  mounted() {
    const active = this.$route.path;
    this.active = active;
  },
  watch: {
    $route() {
      // const item = this.tabList.find((t) => t.name === this.$route.path)s
      this.active = this.$route.path;
    },
  },
  methods: {
    change() {
      this.$router.push({
        path: this.active,
      });
    },
  },
};
</script>